@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Barlow", sans-serif;
}
.sidebar {
  -webkit-flex: 0.2 1;
          flex: 0.2 1;
  margin-top: 10px;
}
.sidebar-shrink {
  -webkit-flex: 0.01 1;
          flex: 0.01 1;
  margin-top: 10px;
}
.videoRow {
  -webkit-flex: 0.8 1;
          flex: 0.8 1;
}
.videoRow-full {
  -webkit-flex: 0.98 1;
          flex: 0.98 1;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.appLayout{
    display: -webkit-flex;
    display: flex;
    
}
.header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 100;
  padding: 8px 0rem;
  padding-right: 1rem;
}
.headerLeft {
  color: rgb(95, 95, 95);
  cursor: pointer;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.headerLogo {
  height: 22px;
  object-fit: contain;
  margin-left: 1rem;
}
.headerInput {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 40%;
  border: 1px solid lightgray;
}

.headerInput input {
  -webkit-flex: 1 1;
          flex: 1 1;
  border: none;
  outline: none;
  font-size: 1rem;
  padding: 4px 1rem;
}
.searchBtn {
  background-color: #f8f8f8;
  color: gray;
  border-left: 1px solid lightgray;
  width: 3rem !important;
  cursor: pointer;
}
.searchBtn:hover {
  background-color: #eeeded;
  color: black;
}
.headerIcons {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.avatar {
  cursor: pointer;
}

.headerIcon {
  cursor: pointer;
  margin-right: 0.3rem;
  color: rgb(95, 95, 95);
  font-weight: bolder;
  padding: 1px 1rem;
}
@media only screen and (max-width: 510px) {
  .headerIcons {
    display: none;
  }
  .header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: left;
            justify-content: left;
  }
  /* .headerAvatar{
        margin-left: 24vw;
    } */
}

.sidebar:hover {
  overflow-x: hidden;
}
.hr {
  color: lightgray;
  margin: 15px 0;
  background-color: lightgray;
  height: 1px;
}
.subscription h2 {
  font-size: 0.9rem;
  margin-top: 1rem;
  margin-left: 1.4rem;
  color: rgb(95, 95, 95);
  font-weight: bolder;
}

.sidebarRow {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px 20px;
  cursor: pointer;
}
.sidebarRow.selected {
  background-color: lightgray;
}
.sidebarRow.selected .icon {
  color: red;
}
.sidebarRow:hover {
  background-color: lightgray;
}
.icon {
  color: rgb(95, 95, 95);
  font-size: 0.8rem;
}
.sidebarRow h2 {
  font-size: 1rem;
  padding-left: 1.3rem;
  font-weight: bolder;
  font-family: "Barlow", sans-serif;
}


.subscriptionRow{
    display: -webkit-flex;
    display: flex;
    margin-top: 20px;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;
}
.subscriptionRow h3{
    font-size: 1rem;
    margin-left: 1rem;
}
.imgLogo{
    /* font-size: 81px !important; */
    height: 100px;
    margin-left: 1rem;
}
.hr {
  color: rgb(233, 233, 233);
  height: 1px;
  border: none;
  margin: 0 10px;
}
.videoComponent {
  background-color: #f7f7f7;
  margin-top: 15px;
  padding-left: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.videoCard {
  width: 300px;
  margin-top: 20px;

  cursor: pointer;
}
.image {
  object-fit: contain;
  /* height: 140px; */
  width: 290px;
}
.videoInfo {
  display: -webkit-flex;
  display: flex;
  margin-left: 1rem;
  margin-top: 4px;
}
.videoText {
  margin-left: 1rem;
  margin-right: 1rem;
}
.videoText h3 {
  font-size: 1rem;
  font-weight: bolder;
}
.desc {
  margin-top: 0.4em;
  font-size: 0.9rem;
  color: rgb(102, 102, 102);
  font-weight: bold;
}

